body {
  font-family: "Poppins", "Trebuchet MS", sans-serif;
  margin: 0 auto;
}

.header {
  padding: 30px;
  background-color: #f9f9fc;
}
.header .header-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .header-menu h3 {
  color: #5E3BEE;
}
.header ul {
  list-style: none;
  display: flex;
  gap: 50px;
}
.header ul a {
  text-decoration: none;
  color: black;
}
.header ul a a:visited {
  text-decoration: none;
  color: black;
}
.header .header-btn {
  color: white;
  background-color: #5E3BEE;
  border-radius: 50px;
  padding: 20px;
  border-style: none;
  width: 124px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  /* tablet */
}
@media screen and (min-width: 0px) and (max-width: 768px) {
  .header .header-btn {
    display: none;
  }
}
@media screen and (min-width: 768px) and (max-width: 1440px) {
  .header .header-btn {
    display: none;
  }
}
.header .menu-head {
  /* tablet */
}
@media screen and (min-width: 0px) and (max-width: 768px) {
  .header .menu-head {
    display: none;
  }
}
@media screen and (min-width: 768px) and (max-width: 1440px) {
  .header .menu-head {
    display: none;
  }
}
@media screen and (min-width: 0px) and (max-width: 768px) {
  .header .side-menu {
    display: block;
    scale: 2;
  }
  .header .side-menu .nav {
    display: flex;
    flex-direction: column;
    background-color: #D9D9D9;
  }
  .header #side-menu:checked ~ .nav {
    display: block !important;
  }
  .header .side-menu:checked ~ .hamb .hamb-line {
    background: transparent;
  }
  .header .side-menu:checked ~ .hamb .hamb-line::before {
    transform: rotate(-45deg);
    top: 0;
  }
  .header .side-menu:checked ~ .hamb .hamb-line::after {
    transform: rotate(45deg);
    top: 0;
  }
}
@media screen and (min-width: 768px) and (max-width: 1440px) {
  .header .side-menu {
    display: block;
    scale: 2;
  }
  .header .side-menu .nav {
    display: flex;
    flex-direction: column;
    background-color: #D9D9D9;
  }
  .header #side-menu:checked ~ .nav {
    display: block !important;
  }
  .header .side-menu:checked ~ .hamb .hamb-line {
    background: transparent;
  }
  .header .side-menu:checked ~ .hamb .hamb-line::before {
    transform: rotate(-45deg);
    top: 0;
  }
  .header .side-menu:checked ~ .hamb .hamb-line::after {
    transform: rotate(45deg);
    top: 0;
  }
}

.side-menu {
  display: none;
